* {
    box-sizing: border-box;
}

html {
    font-family: montserrat;
    line-height: 1.6;
}

body {
    font-size: rem(16px);
    margin: 0;
    margin: auto;
}

h1, h2, h3, h4, h5, h6 {
    font-family: caveat;
    margin: 0 0 0;
    line-height: 1;
    font-weight: 400;
}


section, main, footer {
    
}

img {
    width: 100%;
}

p {
    margin: 0 0 1em;
}

a {
    text-decoration: none;
}
a:hover {
    text-decoration: none;
}

ul{
    list-style-type: none; 
}

